<template>
   <div>
        <loading :active.sync="isLoading"></loading>
        <Navbar></Navbar>
        <div class="jumbotron jumbotron-fluid d-flex align-items-center bg-cover accessoryBanner" style="min-height: 400px;">
            <div class="container accessorySlogan text-center text-gold mx-auto py-5 d-none d-sm-block">
                <h3 class="accessoryTitle">前往遙遠的彼方</h3>
                <p class="lead mx-auto">渴望一趟永不回頭的旅程</p>
            </div>
        </div>
        <div class="container-fluid">
          <div class="row justify-content-center">
            <div class="col-lg-9">
                <div class="row justify-content-around mb-5">
                    <div class="col-md-3 order-3 order-md-1 d-flex flex-column justify-content-center">
                        <ul class="nav">
                            <li class="nav-item">
                                <a class="nav-link text-lightDark font-weight-bold" @click.prevent="sortItem('high')" href="#">價格高至低</a>
                            </li>
                            <li class="nav-item">
                                <a class="nav-link text-lightDark font-weight-bold" @click.prevent="sortItem('low')" href="#">價格低至高</a>
                            </li>
                        </ul>
                    </div>
                    <div class="col-md-6 order-1 order-md-2 d-flex justify-content-center mt-auto"><h3 class="text-gold font-weight-bold mb-4">選購配件</h3></div>
                    <div class="col-md-3 order-2 order-md-3">
                        <div class="form-group">
                            <label for="category" class="text-lightDark font-weight-bold">商品分類</label>
                            <select class="form-control border-paperDark text-lightDark font-weight-bold rounded-0" id="category" @change="filterItem(filter)" v-model="filter">
                                <option value="">所有項目</option>
                                <option value="glass">風鏡</option>
                                <option value="sidebox">置物箱</option>
                                <option value="turbo">渦輪</option>
                                <option value="seat">座墊</option>
                            </select>
                        </div>
                    </div>
                </div>
                <div class="row" v-if="filterData">
                    <div class="col-md-4  mb-3" v-for="item in filterData" :key="item.id"> 
                        <div class="card h-100 border-gold rounded-0"> 
                            <div class="picWrap">
                                <div class="cartbtnWarp bg-cover card-img-top rounded-0" :style='`background-image:url(${item.imageUrl}); height:200px;`'>
                                    <button type="button" class="btn infoIcon" @click="openModal(item)"><i class="fas fa-info-circle text-gold"></i></button>
                                    <button type="button" class="cartbtn btn btn-block text-gold btn-lightDark py-2 btn-sm rounded-0" @click="addtoCart(item)">
                                        <i class="icon fas fa-shopping-basket mr-2"></i>
                                        <span class="font-lg">加入購物車</span>
                                        <i class="icon fas fa-spinner fa-spin ml-2" v-if="item.id === status.loadingItem"></i>
                                    </button> 
                                </div>
                            </div>
                            
                            <div class="card-body">
                                <h5 class="card-title">{{ item.title }}</h5>
                                <p :class='{"text-line-through" : item.price !== item.origin_price, "text-info" : item.price == item.origin_price }'>原價: {{ item.origin_price }}</p>
                                <strong v-if="item.price !== item.origin_price" class="text-danger">特價: {{ item.price }}</strong>
                            </div>
                            
                            
                        </div>
                    </div>
                </div>
            </div>
          </div>    
        </div>
        <Footer></Footer>

        <!--Accessories-info-modal-->
        <div class="modal" id="Accessories" tabindex="-1" role="dialog">
            <div class="modal-dialog modal-lg" role="document">
                <div class="modal-content rounded-0">
                <div class="modal-header bg-lightDark text-gold rounded-0">
                    <h5 v-if="!addCart" class="modal-title">商品資訊</h5>
                    <h5 v-else class="modal-title">商品 "<span class="text-gold" v-text="product.title"></span>" 已放入購物車</h5>
                    <button type="button" class="close" data-dismiss="modal" aria-label="Close">
                    <span aria-hidden="true">&times;</span>
                    </button>
                </div>
                <div class="modal-body font-lg">
                    <div class="container">
                       <div class="row">
                          <div class="col-md-6">
                            <img :src="product.imageUrl" class="img-fluid">
                          </div>
                          <div class="col-md-6">
                            <h3>{{ product.title }}</h3>
                            <p v-html="product.content"></p>
                          </div>
                       </div>
                    </div>
                </div>
                <div class="modal-footer">
                    <button type="button" class="btn btn-secondary" data-dismiss="modal">Close</button>
                </div>
                </div>
            </div>
        </div> 
   </div>
</template>

<script>
import $ from 'jquery';
import { mapGetters } from 'vuex';

export default {
    data(){
        return {
            product:{},
            search: "",
            filter: "",
            addCart: false,
        };
    },

    methods:{
        getAccessories(){
            this.$store.dispatch('getProducts', '選購配件');
        },
        filterItem(filter){
            const target = "Accessories"
            this.$store.dispatch('filterItem', {target, filter});
        },
        sortItem(sort){
            this.$store.dispatch('sortItem', sort);
        },
        addtoCart(item, qty=1){
            var src= 'accessories';
            var id = item.id;
            this.$store.dispatch("cartModules/addtoCart", {id, qty, src})
            this.product = item;
            this.addCart = true;
        },
        openModal(item){
            this.addCart = false;
            this.product = item;
            $("#Accessories").modal("show");
        }
    },
    computed: {
        ...mapGetters(["isLoading", 'Accessories', 'filterData', 'status'])
    },
    created() {
        this.getAccessories();  
    },
}
</script>
