<template>
  <div class="my-5 row justify-content-center">
    <div class="col-md-5">
        <div class="bg-lightGrey">
            <h3 class="text-center py-3 bg-lightDark text-gold">確認資料</h3>
            <table class="table text-lightDark font-weight-bold">
                <tbody>
                    <tr>
                        <th class="border-0" width="100">Email:</th>
                        <td class="border-0">{{ order.user.email }}</td>
                    </tr>
                    <tr>
                        <th class="border-0">姓名:</th>
                        <td class="border-0">{{ order.user.name }}</td>
                    </tr>
                    <tr>
                        <th class="border-0">收件人電話:</th>
                        <td class="border-0">{{ order.user.tel }}</td>
                    </tr>
                    <tr>
                        <th class="border-0">收件人地址:</th>
                        <td class="border-0">{{ order.user.address }}</td>
                    </tr>
                    <tr>
                        <th class="border-0">付款狀態:</th>
                        <td class="border-0">
                            <span v-if="!order.is_paid">尚未付款</span>
                            <span v-else class="text-success">付款完成</span>
                        </td>
                    </tr>
                </tbody>
            </table>
            <div class="text-right" v-if="order.is_paid === false">
                <button type="button" @click="payOrder" class="btn btn-block btn-gold text-lightDark py-3 font-lg font-weight-bold rounded-0">確認付款去</button>
            </div>
        </div>
        
    </div>
    <form class="col-md-5">
        <div class="border border-lightGrey">
            <h3 class="text-center py-3 text-gold bg-lightGrey">訂單明細</h3>
            <table class="table text-lightDark font-weight-bold">
                <thead>
                    <th class="border-0">品名</th>
                    <th class="border-0">數量</th>
                    <th class="border-0">單價</th>
                </thead>
                <tbody>
                    <tr v-for="item in order.products" :key="item.id">
                        <td class="align-middle border-0">{{ item.product.title }}</td>
                        <td class="align-middle border-0">{{ item.qty }}/{{ item.product.unit }}</td>
                        <td class="align-middle border-0 text-right">{{ item.final_total | currency}}</td>
                    </tr>
                </tbody>
                <tfoot>
                    <tr class="font-lg text-gold">
                        <td colspan="2" class="text-right border-0">總計</td>
                        <td class="text-right border-0">{{ order.total | currency }}</td>
                    </tr>
                </tfoot>
            </table>
        </div>
        
    </form>
  </div>
</template>


<script>
export default {
    data(){
        return {
            order:{
                user:{}
            },
            order_id:"",
        };
    },
    
    methods: {
        getOrder(){
            const vm = this;
            const api = `${process.env.VUE_APP_APIPATH}/api/${process.env.VUE_APP_CUSTOMPATH}/order/${vm.order_id}`;

            vm.isLoading = true;
            vm.$http.get(api).then((response) => {   
                vm.order = response.data.order;
                vm.isLoading = false;
            })
        },

        payOrder(){
            const vm=this;
            const api=`${process.env.VUE_APP_APIPATH}/api/${process.env.VUE_APP_CUSTOMPATH}/pay/${vm.order_id}`;

            vm.isLoading = true;
            vm.$http.post(api).then((response) => {   
                if(response.data.success){
                    vm.getOrder();
                    alert("付款成功")
                }
                vm.isLoading = false;
            })
        }
    },

    created() {
        this.order_id=this.$route.params.orderID;
        this.getOrder();
    },

}
</script>